import { Injectable } from '@angular/core';
import { HttpClient, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { AvlUploadService, AvlFile, AvlAbortUploadAction, AvlUploadProgressEvent, AvlHttpErrorResponse } from '@avl-controls/interfaces';

import { map, catchError } from 'rxjs/operators';
import { Observable, of } from 'rxjs';

@Injectable()
export class UploadService implements AvlUploadService {
  constructor(private httpClient: HttpClient) { }

  downloadFile(
    url: string,
    progressCallback?: (progress: AvlUploadProgressEvent) => void,
    completeCallback?: (blob: Blob) => void,
    failCallback?: (errorResponse: AvlHttpErrorResponse) => void
  ): AvlAbortUploadAction {
    const req: Observable<any> = this.httpClient.get(url, { reportProgress: true, responseType: "blob", observe: 'events' })
      .pipe(map(event => {
        if (event.type === HttpEventType.DownloadProgress) {
          progressCallback?.(event);
        }
        if (event.type === HttpEventType.Response && event.body != null) {
          completeCallback?.(event.body);
        }
      }), catchError((err: HttpErrorResponse) => {
        const response = err.error instanceof Event ? (<XMLHttpRequest>err.error.target).response : err.error;
        if (failCallback) failCallback({
          status: err.status,
          response: response
        });
        return of(null);
      }));
    const subscription = req.subscribe();
    return subscription.unsubscribe.bind(subscription);
  }

  uploadFile(
    file: AvlFile,
    target: string,
    progressCallback?: (progress: AvlUploadProgressEvent) => void,
    completeCallback?: (downloadUrl?: string) => void,
    failCallback?: (errorResponse: AvlHttpErrorResponse) => void
  ): AvlAbortUploadAction {
    const formData = new FormData();
    formData.append('file', file.file);
    const uploadReq: Observable<any> = this.httpClient.post<{downloadUrl?: string}>(target, formData, { reportProgress: true, observe: 'events' } )
      .pipe(map(event => {
        if (event.type === HttpEventType.UploadProgress) {
          progressCallback?.(event);
        }
        if(event.type === HttpEventType.Response) {
          if (event.body != null && event.body.downloadUrl) {
            completeCallback?.(event.body.downloadUrl);
          }
          else {
            completeCallback?.();
          }
        }
      }), catchError( (err: HttpErrorResponse, caught) => {
        const response = err.error instanceof Event ? (<XMLHttpRequest>err.error.target).response : err.error;
        if(failCallback) failCallback({
          status: err.status,
          response: response
        });
        return of(null);
      }));
      const subscription = uploadReq.subscribe();
      return subscription.unsubscribe.bind(subscription);
  }
}
