import { Directive, ElementRef, OnInit, HostListener, Output, EventEmitter, Inject } from '@angular/core';
import { AvlFile, AvlUploadService } from '@avl-controls/interfaces';
import { UPLOAD_SERVICE } from '../services/variables';

@Directive({
  selector: 'avl-upload',
})
export class AvlUploadDirective implements OnInit {
  @Output() uploadComplete: EventEmitter<AvlFile> = new EventEmitter();
  @Output() uploadAbort: EventEmitter<AvlFile> = new EventEmitter();
  @Output() uploadFail: EventEmitter<any> = new EventEmitter();
  @Output() uploadStart: EventEmitter<AvlFile> = new EventEmitter();
  @Output() filesChanged: EventEmitter<AvlFile[]> = new EventEmitter();
  @Output() fileRejected: EventEmitter<AvlFile> = new EventEmitter();
  @Output() menuItemClicked: EventEmitter<any> = new EventEmitter();
  @Output() downloadFail: EventEmitter<any> = new EventEmitter();
  @Output() downloadAbort: EventEmitter<AvlFile> = new EventEmitter();

  constructor(private element: ElementRef, @Inject(UPLOAD_SERVICE) private uploadService: AvlUploadService) {}

  ngOnInit() {
    const element = <any>this.element.nativeElement;
    element.uploadService = this.uploadService;
  }

  @HostListener('upload-complete', ['$event'])
  uploadCompleteHandler(e: CustomEvent) {
    this.uploadComplete.emit(e.detail.value);
  }

  @HostListener('upload-abort', ['$event'])
  uploadCanceledHandler(e: CustomEvent) {
    this.uploadAbort.emit(e.detail.value);
  }

  @HostListener('upload-start', ['$event'])
  uploadStartedHandler(e: CustomEvent) {
    this.uploadStart.emit(e.detail.value);
  }

  @HostListener('upload-fail', ['$event'])
  uploadFailedHandler(e: CustomEvent) {
    this.uploadFail.emit(e.detail);
  }

  @HostListener('files-changed', ['$event'])
  filesChangedHandler(e: CustomEvent) {
    this.filesChanged.emit(e.detail.value);
  }

  @HostListener('file-rejected', ['$event'])
  fileRejectedHandler(e: CustomEvent) {
    this.fileRejected.emit(e.detail.value);
  }

  @HostListener('menu-item-clicked', ['$event'])
  menuItemClickedHandler(e: CustomEvent) {
    this.menuItemClicked.emit(e.detail);
  }

  @HostListener('download-fail', ['$event'])
  downloadFailedHandler(e: CustomEvent) {
    this.downloadFail.emit(e.detail);
  }

  @HostListener('download-abort', ['$event'])
  downloadCanceledHandler(e: CustomEvent) {
    this.downloadAbort.emit(e.detail.value);
  }
}
