import { Directive, EventEmitter, Output, HostListener } from '@angular/core';
import { addEventListenerForEmittingChangedEvents } from '../binding-utils';
@Directive({
  selector: 'avl-snackbar'
})
export class AvlSnackbarDirective {
  @Output() buttonClicked: EventEmitter<boolean> = new EventEmitter();
  @Output() displayedChange: EventEmitter<boolean> = new EventEmitter();

  @HostListener('button-clicked', ['$event'])
  buttonClickedHandler(e: CustomEvent) {
    this.buttonClicked.emit(e.detail.value);
  }

  @HostListener('displayed-changed', ['$event'])
  displayedChangeHandler(e: CustomEvent) {
    this.displayedChange.emit(e.detail.value);
  }
}
