import { AvlTranslationService } from '@avl-controls/interfaces/src/avl-translation-service';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Injectable()
export class NgxTranslateTranslationService implements AvlTranslationService {

  public get language(): string {
    return this.translateService.currentLang;
  }

  constructor(private translateService: TranslateService) { }

  public translate(key: string, interpolateParams?: object): Promise<string> {
    return this.translateService.get(key, interpolateParams).toPromise();
  }
}
